import React, { Component } from 'react';
import axios from 'axios';

class Like extends Component {
    state = {
        feed_id: this.props.id,
        like: true,
        likeCount: this.props.likes,
    }

    /**
     *   '' 
     * ''   ++,      --
     */
    likeCmt = () => {
        const {onUpdate} = this.props;
        let {like, likeCount} = this.state;

        if (like) {
            likeCount++;

        } else {
            likeCount--;
        }
        
        onUpdate(likeCount);

        this.setState({
            like: !like,
            likeCount: likeCount,
        });
    }

    handleLikeCount = () => {
        let {feed_id, like, likeCount} = this.state;

        if (like) {
            //  
            axios.put('/good/' + feed_id, {})
            .then( response => {
                const responseData = response.data;
                const successValue = responseData.success;
                if (successValue === 1) {
                    likeCount++;

                    this.setState({
                        like: !like,
                        likeCount: likeCount,
                    });
                }
            } )
            .catch( response => { console.log(response) } );

        } else {
            //  
            axios.delete('/good/' + feed_id, {})
            .then( response => {
                const responseData = response.data;
                const successValue = responseData.success;
                if (successValue === 1) {
                    likeCount--;

                    this.setState({
                        like: !like,
                        likeCount: likeCount,
                    });
                }
            } )
            .catch( response => { console.log(response) } );
        }
    }

    render() {
        const {likeCount} = this.state;

        return (
            <div className="likeArea">
                <input type="button" value="" onClick={this.handleLikeCount}></input>
                <span>{likeCount}</span>
            </div>
        );
    }
}

export default Like;