import React, { Component } from 'react';

class CommentForm extends Component {
    state = {
        content: '',
    }

    handleSubmit = (e) => {
        // preventDefault: submit    
        e.preventDefault();
        const {content} = this.state;
        // content     
        //  button disabled/abled  
        if (content) {
            this.props.onCreate(content);
            // submit  
            this.setState({
                content: '',
            });
        }
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    handleKeyUp = (e) => {
        e.preventDefault();
        // Enter      
        if (e.keyCode === 13) {
            this.handleSubmit(e);
        }
    }

    render() {
        const {type} = this.props;

        return (
            <form className={(type === "comment") ? "commentFormArea" : "replyFormArea"}
                    onKeyUp={this.handleKeyUp}>
                {/* <div className="inputAuthor">
                    <input name="author" className="inputId"
                        value={this.state.author}
                        placeholder=""
                        onChange={this.handleChange}></input>
                </div> */}
                <div className="inputForm">
                    <textarea className="inputContent" name="content"
                    value={this.state.content}
                    placeholder=" "
                    onChange={this.handleChange}></textarea>
                    <div className="submitArea">
                        <input type="button" className="submitBtn"
                        onClick={this.handleSubmit}
                        value=""/>
                    </div>
                </div>
            </form>
        );
    }
}

export default CommentForm;