import {combineReducers} from 'redux';
import * as types from '../actions/ActionTypes'

const info = {
    logged: false,
    joined: false
}

/**
 * Reducer  
 * state undefined (  ) state   info 
 * state   ,
 *           
 * @param {*} state 
 * @param {*} action 
 */
const infoFunc = (state = info, action = types.ISAUTH) => {
    console.log(action);
    switch (action.type) {
        case types.ISAUTH:
            console.log("infoFunc logged == " + state.logged);
            return {...state, logged: !state.logged}
        case types.ISJOIN:
            console.log("infoFunc joined == " + state.joined);
            return {...state, joined: !state.joined}
        default:
            return state;
    }
}

// const checkValidFunc = (state, action = types.ISVALID) => {

// }

// store (combine)
const reducers = combineReducers({
    infoFunc
});

export default reducers;