import React, { Component } from 'react';
import PopUpMenu from './PopUpMenu';

class Share extends Component {
    state = {
        shareCount: this.props.shareCount,
        menuOpen: false,
        shareMenu: [{
            id: 0,
            title: '  ',
            selected: false,
        },
        {
            id: 1,
            title: ' ',
            selected: false,
        },
        {
            id: 2,
            title: ' ',
            selected: false,
        }]
    }

    //    count 
    updateShareCount = (count) => {
        const {onUpdate} = this.props;
        
        onUpdate(count);

        this.setState({
            shareCount: count,
        });
    }

    //   menu close ( )
    handleClickOutside(){
        this.setState({
            menuOpen: false,
        });
    }

    // menu open/close toggle
    toggleList = () => {
        this.setState(prevState => ({
            menuOpen: !prevState.menuOpen,
        }));
    }

    render() {
        const {shareCount, menuOpen, shareMenu} = this.state;

        return (
            <div className="shareArea">
                <input type="button" value="" onClick={this.toggleList}></input>
                <span>{shareCount}</span>
                {
                    menuOpen && <PopUpMenu menuList={shareMenu} count={shareCount}
                                            onUpdate={this.updateShareCount}/>
                }
            </div>
        );
    }
}

export default Share;