import React, { Component } from 'react';

class FromNow extends Component {
    //  
    getTimeAgo = () => {
        const time = this.props.time;
        console.log("before_time = " + time);
        //     
        const cur_time = new Date().getTime();
        console.log("cur_time = " + cur_time);
        let second = (cur_time - time) / 1000;
        let year, month, day, hour, minute, elapsed;

        if (second > 60) {
            minute = second / 60;
            if (minute > 60) {
                hour = minute / 60;
                if (hour > 24) {
                    day = hour / 24;
                    if (day > 30) {
                        month = day / 30;
                        if (month > 12) {
                            year = month / 12;
                            elapsed = parseInt(year) + "";
                        } else {
                            elapsed = parseInt(month) + "";
                        }
                    } else {
                        elapsed = parseInt(day) + "";
                    }
                } else {
                    elapsed = parseInt(hour) + "";
                }
            } else {
                elapsed = parseInt(minute) + "";
            }
        } else {
            elapsed = " ";
        }

        return elapsed
    }

    render() {
        return (
            <div className="time">
                <span className="fromNow">{this.getTimeAgo()} </span>
            </div>
        );
    }
}

export default FromNow;