import React, { Component } from 'react';
import CardUser from './CardUser';
import CardContents from './CardContents';
import CardAdded from './CardAdded';
import CardTag from './CardTag';
import CardUtil from './CardUtil';
import Toast from '../Ui/Toast';
import Popup from '../Ui/Popup/';
import Confirm from '../Ui/Confirm';


import Detail from '../Detail/Detail';
import Write from '../Write/Write';

import './Card.scss';
import '../Ui/Ui.scss';


class Card extends Component {
  state = {
    toast: false,
    popup: false,
    confirm: false,
    detail:false,
    write:false,
    type: '',
  }

  //  
  toastMsg = (tstmsg)=>{ //  
    let msg = tstmsg;

    this.setState({
      toast: true,
      msg: msg,
    });
    setTimeout(()=> this.setState({toast: false}), 1000); // 1  hide
  }

  // 
  openPopup = (act, tit)=>{ //  (ex: report, collections...),  
    let type = act,
        title = tit;

    this.setState({
      popup: true,
      type: type,
      title: title,
    });
  }
  closePopup = ()=>{
    this.setState({popup: false});
  }

  // 
  openConfirm = (act, tit, cont)=>{ // (   ) (ex: mute, del...),  ,  
    let type = act,
        title = tit,
        html = cont;

    this.setState({
      confirm: true,
      type: type,
      title: title,
      html: html,
    });
  }
  closeConfirm = () =>{
    this.setState({confirm: false});
  }

  // ()
  openDetail = () =>{
    // this.setState({detail: true});
    const cardID = this.props.card.feed_id;
    this.props.history.push('/posts/' + cardID);
  }
  
  closeDetail = () =>{
    this.setState({detail: false});
  }

  // ()/
  openWrite = () =>{
    this.setState({write: true});
  }
  closeWrite = () =>{
    this.setState({write: false});
  }


  render() {

    const {card} = this.props;

    return (
      <>

        {/*  */}
        <div className="card">
          <CardUser id={card.feed_id}
            confirm={this.openConfirm}
            popup={this.openPopup}
            completeMsg={this.toastMsg}
            write={this.openWrite}
            time={card.build_date}
          />
          
          <CardContents
            view={this.openDetail}
            title={card.title}
            contents={card.content}
          />

          <CardAdded
            view={this.openDetail}
          />

          <CardTag />

          <CardUtil
            confirm={this.openConfirm}
            popup={this.openPopup}
            completeMsg={this.toastMsg}
            view={this.openDetail}
            write={this.openWrite}
            id={card.feed_id}
            liked={card.good}
          />
        </div>

      
        {this.state.toast && 
          <Toast 
            msg={this.state.msg}
          /> 
        }

        {this.state.confirm && 
          <Confirm
            title={this.state.title}
            html={this.state.html}
            type={this.state.type}
            msg={this.state.msg}
            close={this.closeConfirm}
            completeMsg={this.toastMsg}
           /> 
        }
        
        {this.state.popup &&
          <Popup
            title={this.state.title}
            type={this.state.type}
            close={this.closePopup}
            completeMsg={this.toastMsg}
          /> 
        }


        {this.state.detail && 
          <Detail
            close={this.closeDetail}
           />
        }

        {this.state.write && 
          <Write
            close={this.closeWrite}
           />
        }

      </>
    );
  }
}

export default Card;