import React, { Component } from 'react';
import Card from '../Card/Card';
import Toast from '../Ui/Toast';
import Popup from '../Ui/Popup/';
import Confirm from '../Ui/Confirm';
import Detail from '../Detail/Detail';
import Write from '../Write/Write';

import '../Card/Card.scss';
import '../Ui/Ui.scss';
import axios from 'axios';

class List extends Component {

  state = {
    feed: [],
    toast: false,
    popup: false,
    confirm: false,
    detail:false,
    write:false,
    type: '',
  }

  //  
  toastMsg = (tstmsg)=>{ //  
    let msg = tstmsg;

    this.setState({
      toast: true,
      msg: msg,
    });
    setTimeout(()=> this.setState({toast: false}), 1000); // 1  hide
  }

  // 
  openPopup = (act, tit)=>{ //  (ex: report, collections...),  
    let type = act,
        title = tit;

    this.setState({
      popup: true,
      type: type,
      title: title,
    });
  }
  closePopup = ()=>{
    this.setState({popup: false});
  }

  // 
  openConfirm = (act, tit, cont)=>{ // (   ) (ex: mute, del...),  ,  
    let type = act,
        title = tit,
        html = cont;

    this.setState({
      confirm: true,
      type: type,
      title: title,
      html: html,
    });
  }
  closeConfirm = () =>{
    this.setState({confirm: false});
  }

  // ()
  openDetail = () =>{
    this.setState({detail: true});
  }
  closeDetail = () =>{
    this.setState({detail: false});
  }

  // ()/
  openWrite = () =>{
    this.setState({write: true});
  }
  closeWrite = () =>{
    this.setState({write: false});
  }

  componentDidMount() {
    axios.get('http://dev-jolse.iptime.org:9000/feed').then((response) => {
      this.setState({
        feed: response.data.results
      })
    });
  }


  render() {

    const { feed } = this.state;
    const { history } = this.props;

    return (
      <>

        {/* <Login /> */}
        
        {/*  */}

        {feed.map((f, i) => {
          return (
            <Card confirm={this.openConfirm}
              popup={this.openPopup}
              completeMsg={this.toastMsg}
              write={this.openWrite}
              view={this.openDetail}
              history={history}
              card={feed[i]}
              key={i}
            />
          );
        })}
        
      
        {this.state.toast && 
          <Toast 
            msg={this.state.msg}
          /> 
        }

        {this.state.confirm && 
          <Confirm
            title={this.state.title}
            html={this.state.html}
            type={this.state.type}
            msg={this.state.msg}
            close={this.closeConfirm}
            completeMsg={this.toastMsg}
           /> 
        }
        
        {this.state.popup &&
          <Popup
            title={this.state.title}
            type={this.state.type}
            close={this.closePopup}
            completeMsg={this.toastMsg}
          /> 
        }


        {this.state.detail && 
          <Detail
            close={this.closeDetail}
           />
        }

        {this.state.write && 
          <Write
            close={this.closeWrite}
           />
        }

      </>
    );
  }
}

export default List;