import React, { Component } from 'react';
import CommentForm from './CommentForm';
import CommentList from './CommentList';
import axios from 'axios';

class CommentView extends Component {
    //   id
    comment_id = 3;
    reply_id = 2;

    static defaultProps = {
        comments: []
    }

    state = {
        sortBy: "likes",
        feed_id: this.props.id,
        comments: this.props.comments,
    }

    /**
     *   (:  )
     *   on/off,
     *       
     */
    handleSort = (e) => {
        let {sortBy} = this.state;
        const type = e.target.getAttribute('name');

        if (sortBy !== type) {
            this.setState({
                sortBy: type,
            });
        }
    }

    //   
    shouldComponentUpdate(nextProps, nextState) {
        if (this.state !== nextState) {
            return true;
        }
        
        return this.props.comments !== nextProps.comments;
    }

    componentDidMount () {
        console.log("### CommentView ComponentDidMount");
        this.getComments();
    }

    //   
    getComments = () => {
        const {comments} = this.props;
        let commentList = this.state.comments;
        this.setState({
            comments: commentList.concat(comments),
        });
    }

    //  
    handleSubmitComment = (data) => {
        const {feed_id} = this.state;
        let {comments} = this.state;

        axios.post('http://dev-jolse.iptime.org:9000/comment/' + feed_id, {
            comment: data,
        })
        .then( response => {
            const responseData = response.data;
            const successValue = responseData.success;
            if (successValue === 1) {
                //  !
                let comment_id = responseData.insertId;
                console.log("### comment  ! id = " + comment_id);
                this.setState({
                    comments: comments.concat({
                        id: comment_id,
                        comment: data,
                        feed_id: feed_id,
                    })
                });
            }
        } )
        .catch( response => { console.log(response) } );
    }

    //  
    handleUpdateComment = (id, data) => {
        const comment_id = id;
        let {comments} = this.state;
        console.log(comments);
        axios.put('http://dev-jolse.iptime.org:9000/comment/' + comment_id, {
            comment: data,
        })
        .then( response => {
            const responseData = response.data;
            const successValue = responseData.success;
            if (successValue === 1) {
                //   !
                console.log("### comment  ! id = " + comment_id);

                this.setState({
                    comments: comments.map(
                        info => {
                            console.log(info.id + "===" + comment_id);
                            if (info.id === comment_id) {
                                return {
                                    id: comment_id,
                                    comment: data,
                                    feed_id: this.state.feed_id,
                                };
                            }
                            return info;
                        }
                    )
                });
            }
        } )
        .catch( response => { console.log(response) } );
    }

    //  
    handleDeleteCommit = (id) => {
        const comment_id = id;
        let {comments} = this.state;

        axios.delete('http://dev-jolse.iptime.org:9000/comment/' + comment_id, {})
        .then( response => {
            const responseData = response.data;
            const successValue = responseData.success;
            if (successValue === 1) {
                //   !
                console.log("### comment  ! id = " + comment_id);
                console.dir(comments);
                this.setState({
                    comments: comments.filter(info => info.id !== comment_id)
                });
            }
        } )
        .catch( response => { console.log(response) } );
    }

    //  
    handleCreateReply = (id, data) => {
        const {information} = this.state;

        this.setState({
            information: information.map(
                info => {
                    if (info.id === id) {
                        return {
                            id: info.id,
                            author: info.author,
                            content: info.content,
                            likes: info.likes,
                            time: info.time,
                            reply: info.reply.concat({
                                //    id 
                                id: info.id + "-" + this.reply_id++,
                                ...data
                            })
                        }
                    }
                    return info;
                }
            )
        });
    }

    //  
    handleUpdate = (id, data) => {
        let {information} = this.state;
        // id  type /  
        const isReply = (typeof(id) === 'string');

        if (isReply) {
            //   
            const idArr = id.split('-');
            const commentId = idArr[0];

            this.setState({
                information: information.map(
                    info => {
                        if (info.id.toString() === commentId) {
                            let reply = info.reply;
                            return {
                                id: info.id,
                                author: info.author,
                                content: info.content,
                                likes: info.likes,
                                time: info.time,
                                reply: reply.map(
                                    rep => {
                                        if (rep.id === id) {
                                            return {
                                                id: rep.id,
                                                author: rep.author,
                                                time: rep.time,
                                                likes: rep.likes,
                                                ...data
                                            }
                                        }
                                        return rep;
                                })
                                
                            }
                        }
                        return info;
                    }
                )
            });
            
        } else {
            //   
            this.setState({
                information: information.map(
                    info => {
                        if (info.id === id) {
                            return {
                                id: id,
                                author: info.author,
                                time: info.time,
                                likes: info.likes,
                                reply: info.reply,
                                ...data,
                            };
                        }
                        return info;
                    }
                )
            });
        }
    }

    //   
    updateLikes = (id, likes) => {
        const {information} = this.state;
        // id  type /  
        const isReply = (typeof(id) === 'string');

        if (isReply) {
            //   
            const idArr = id.split('-');
            const commentId = idArr[0];

            this.setState({
                information: information.map(
                    info => {
                        if (info.id.toString() === commentId) {
                            let reply = info.reply;
                            return {
                                id: info.id,
                                author: info.author,
                                content: info.content,
                                likes: info.likes,
                                time: info.time,
                                reply: reply.map(
                                    rep => {
                                        if (rep.id === id) {
                                            return {
                                                id: rep.id,
                                                author: rep.author,
                                                content: rep.content,
                                                time: rep.time,
                                                likes: likes,
                                            }
                                        }
                                        return rep;
                                })
                                
                            }
                        }
                        return info;
                    }
                )
            });

        } else {
            //   
            this.setState({
                information: information.map(
                    info => {
                        if (info.id === id) {
                            return {
                                id: id,
                                author: info.author,
                                content: info.content,
                                likes: likes,
                                time: info.time,
                                reply: info.reply,
                            };
                        }
                        return info;
                    }
                )
            });
        }
    }

    //  
    handleRemove = (id) => {
        const {information} = this.state;
        // id  type /  
        const isReply = (typeof(id) === 'string');

        if (isReply) {
            //  
            const idArr = id.split('-');
            const commentId = idArr[0];

            this.setState({
                information: information.map(
                    info => {
                        if (info.id.toString() === commentId) {
                            let reply = info.reply;
                            return {
                                id: info.id,
                                author: info.author,
                                content: info.content,
                                likes: info.likes,
                                time: info.time,
                                reply: reply.filter(rep => rep.id !== id),
                            }
                        }
                        return info;
                    }
                )
            });

        } else {
            //  
            this.setState({
                information: information.filter(info => info.id !== id)
            });
        }        
    }
    
    render() {
        let {comments} = this.props;
        let {sortBy} = this.state;
        const arr_len = comments.length;

        // (/)   
        comments = comments.sort(function(a, b) {
            return b[sortBy] - a[sortBy];
        });

        return (
            <div className="commentListWrap">
                <div className="commentListFunc">
                    <div className="commentCount">
                        <span>{arr_len}</span><span> </span>
                    </div>
                    <div className="commentSort">
                        <div className={(sortBy === "likes") ? "sortSelected" : "sort"}
                            onClick={this.handleSort}>
                            <span name="likes"></span>
                        </div>
                        <div className="sortSep"></div>
                        <div className={(sortBy === "time") ? "sortSelected" : "sort"}
                            onClick={this.handleSort}>
                            <span name="time"></span>
                        </div>
                    </div>
                </div>
                <div>
                    <CommentList data={comments}
                    onUpdate={this.handleUpdateComment}
                    onRemove={this.handleDeleteCommit}
                    updateLikes={this.updateLikes}
                    onCreateReply={this.handleCreateReply}/>
                    <hr />
                    <CommentForm type="comment" onCreate={this.handleSubmitComment}/>
                </div>
            </div>
        );
    }
}

export default CommentView;