import React, { Component } from 'react';

class Confirm extends Component {
  state = {
    confirm: false,
    type: '',
  }
  static defaultProps = {
    title: ' ',
    html: ' ',
  }

  completeMsg = ()=>{
    let msg = this.props.msg,
        type = this.props.type;

    if (type === 'mute'){
      msg = '.'
    }
    if (type === 'del'){
      msg = '.'
    }
    this.props.completeMsg(msg);
    this.props.close();
  }

  closeDimm = (e)=>{
    let dimm = e.target.className;
    if (dimm === 'box') {
      this.props.close()
    }
  }
  
  render() {
    
    return (
      <div id="confirm" onClick={this.closeDimm}>
        <div className="box">
          <div className="frame">
            <button type="button" className="btnClose" onClick={this.props.close}><i className="fas fa-times"></i><span className="blind"></span></button>
            <div className="title">{this.props.title}</div>
            <div className="entry">{this.props.html}</div>
            <div className="btns">
              <button type="button" className="btn" onClick={this.props.close}></button>
              <button type="button" className="btn red" onClick={this.completeMsg}></button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Confirm;